@keyframes fadeIn {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}


@keyframes slideIn {
  from {
      opacity: 0;
      transform: translateX(-40%);
  }
  to {
      opacity: 1;
      transform: translateX(0%);
  }
}

@keyframes scaleUp {
  from {
      opacity: 0;
      transform: scale(0.5);
  }
  to {
      opacity: 1;
      transform: scale(1);
  }
}

